  methods: {
    toggleMenu() {
      if (this.slideIndex > 4) this.slideIndex = 1;

      setTimeout(() => {
        this.slideIndex = Number(this.slideIndex) + 1;

        this.toggleMenu();
      }, 10000);
    }
  },
  mounted() {
    setInterval(this.toggleMenu(), 20000);
  },
  destroyed() {
    clearInterval();
    clearTimeout();
  }
};
