<template>
  <footer class="block mt-10 font-poppins">
    <section class="lg:flex lg:justify-evenly bg-blue-100 px-6 py-8">
      <div>
        <h1 class="text-swizel-colorB text-3xl p-4">
          Subscribe to <br class="lg:hidden" />
          our Newsletters
        </h1>

        <p class="flex justify-center px-4 text-sm text-gray-600 text-justify">
          Discover how other institutions all over the world use technology
          <br />
          to create and end up being a step ahead of the competition
        </p>
      </div>

      <div class="">
        <div class="flex justify-center items-center mt-4 space-x-2">
          <input
            type="email"
            name="email"
            id="email"
            placeholder="Type your email here"
            class="p-2 text-xs md:text-sm md:p-3 rounded-full focus:outline-none"
            v-model="email"
          />
          <button
            type="submit"
            class="bg-gray-500 rounded-full text-xs md:text-sm p-1 md:p-3 w-24 text-white focus:outline-none"
            @click="subscribeToNewsletter()"
          >
            Submit
          </button>
        </div>

        <div v-show="dialog" class="font-thin text-xs p-4">
          <p v-if="success" class="text-green-700">
            Thanks, you've successfully subscribed to our Newsletter.
          </p>
          <p v-else class="text-red-700">
            Something went wrong, please try again
          </p>
        </div>
      </div>
    </section>

    <section class="block sm:flex sm:justify-evenly px-4 py-8">
      <div class="p-4">
        <h1 class="text-xl xs:text-center font-hairline text-swizel-colorA">
          CONTACT
        </h1>
        <p class="text-gray-600 xs:text-center">contact@swizel.co</p>
        <p class="text-gray-600 xs:text-center">+249024664900</p>
      </div>

      <div class="p-4">
        <h1 class="text-xl font-hairline text-swizel-colorA xs:text-center">
          EXTRAS
        </h1>
        <p class="xs:text-center">
          <router-link to="/blog" class="text-gray-600">Blog</router-link>
        </p>
        <p class="xs:text-center">
          <router-link to="/privacy-policy" class="text-gray-600"
            >Privacy Policy</router-link
          >
        </p>
      </div>

      <div class="p-4">
        <h1 class="text-xl font-hairline text-swizel-colorA xs:text-center">
          COMPANY
        </h1>
        <p class="xs:text-center">
          <a href="/about" class="text-gray-600">About Swizel</a>
        </p>
        <p class="xs:text-center">
          <a href="#partners" class="text-gray-600">Partners</a>
        </p>
      </div>

      <div class="p-4">
        <p class="text-lg p-2 text-swizel-colorA xs:text-center">find us on:</p>
        <div class="flex xs:justify-center space-x-4">
          <a href="https://facebook.com/swizelhq"
            ><img src="../assets/images/sfb.svg" alt="facebook"
          /></a>

          <a href="https://instagram.com/swizelhq"
            ><img src="../assets/images/sig.svg" alt="instagram"
