      </p>
    </section>
  </footer>
</template>

<script>
import axios from "axios";
export default {
  name: "Footer",
  data() {
    return {
      email: null,
      dialog: false,
      success: false
    };
  },
  methods: {
    subscribeToNewsletter: async function() {
      let data = {
        email: this.email
      };
      try {
        let response = await axios.post(
          "https://swizel-email-api.herokuapp.com/v1/newsLetter/subscribe",
          data
        );
        if (response.status == 200) {
          this.dialog = true;
          this.success = true;

          setTimeout(() => {
            this.dialog = false;
            this.email = null;
          }, 5000);
        }
      } catch (error) {
        this.dialog = true;
        this.success = false;

        setTimeout(() => {
          this.dialog = false;
          this.email = null;
        }, 5000);
      }
    }
  }
};
</script>
